<template>
  <div>
    <v-h1 :back-to="backPath">{{ title }}</v-h1>

    <v-info class="mb-5">App name in the store will be parsed <b>NSBundleName</b> of your app Info.plist</v-info>

    <form class="flex flex-col items-stretch gap-5">
      <div class="grid grid-cols-[1fr_3fr] gap-5">
        <v-label label="Label (internal name)">
          <v-input v-model="form.label" />
        </v-label>

        <v-label label="Lead (short description)">
          <v-input
            v-model="form.lead"
            :max-length="100"
          />
        </v-label>
      </div>

      <v-question>
        <template #question> Why is there no bundle identifier? </template>

        Due to the nature of Apple restrictions for 3rd party app distribution, appdb assigns random bundle identifiers upon app installation to
        devices. In order to support all features for your app, including push, URLs, app groups, keychain and storage, you need to get identifiers in
        runtime dynamically and do not use them as strings.
        <NuxtLink to="https://rtfm.dbservices.to/#/app-development/identifiers"
          >Read documentation about various identifiers that you can use inside your app.</NuxtLink
        >.
      </v-question>

      <div class="flex gap-5">
        <v-label label="License type">
          <v-toggle-buttons
            v-model="form.license_type"
            :options="[
              { label: 'Per user', value: 'per_user' },
              { label: 'Per device', value: 'per_device' },
            ]"
          />
        </v-label>

        <v-label
          label="Category / Genre"
          hint="As genres are in the process of population, if you didn't find your  Please specify genre like âCategory / Genreâ, e.g. âGames / Actionâ, if possible"
        >
          <v-dropdown
            v-model="form.genre_name"
            :options="genreOptions"
            show-clear
            editable
          />
        </v-label>

        <v-label label="Is adult">
          <v-yes-or-no v-model="form.is_18plus" />
        </v-label>

        <v-label
          label="No signing required"
          hint="Select âyesâ if you want to distribute app signed by your enterprise certificate. In this case, appdb will not sign your app in the cloud for each device"
        >
          <v-yes-or-no v-model="form.do_not_sign" />
        </v-label>
      </div>

      <v-label
        label="Associated URIs"
        hint="Specify a mask of URIs that will be redirected to your app (if user enabled redirection). Only https URIs are supported. Conditional matching is possible with asterisk, e.g. https://example.com/*. <NuxtLink to='https://rtfm.dbservices.to/#/services-and-features/deep-linking'>Deep linking documentation</NuxtLink>"
      >
        <v-button @click="form.associated_uris.push('')"> Add URI </v-button>

        <div class="mt-2 grid grid-cols-5 gap-2">
          <div
            v-for="(uri, idx) of form.associated_uris"
            :key="idx"
            class="relative"
          >
            <v-input
              v-model="form.associated_uris[idx]"
              placeholder="Enter URI..."
              :input-attrs="{ class: 'pr-8' }"
            />
            <button
              v-if="form.associated_uris.length > 1"
              class="absolute bottom-px right-2 top-0 text-xl"
              @click.prevent="form.associated_uris.splice(idx, 1)"
            >
              <Icon name="tabler:x" />
            </button>
          </div>
        </div>
      </v-label>

      <div class="grid grid-cols-5 gap-5">
        <v-label label="Terms of service URI">
          <v-input v-model="form.tos_uri" />
        </v-label>

        <v-label label="Privacy policy URI">
          <v-input v-model="form.privacy_policy_uri" />
        </v-label>

        <v-label label="Website URI">
          <v-input v-model="form.website_uri" />
        </v-label>

        <v-label label="Support URI">
          <v-input v-model="form.support_uri" />
        </v-label>

        <v-label
          label="Publication date"
          hint="If you want to delay app publication, by default app is published as soon as IPA file has been processed and approved for distribution. If you want to manually release your app, set publication date in the far future"
        >
          <v-timestamp-input
            v-model="form.publish_at"
            show-time
            :min-date="new Date()"
          />
        </v-label>
      </div>

      <v-button
        variant="primary"
        class="self-start"
        :loading="isSubmitting"
        @click="submit"
      >
        Continue
      </v-button>
    </form>
  </div>
</template>

<script setup lang="ts">
const { title, backPath, id } = useManagePage("app identifier")

const identifier = id.value ? (await getApps({ identifier: id.value }))[0] : null

const form = reactive<Parameters<typeof editApp>[0]>({
  identifier: undefined,
  tos_uri: "",
  privacy_policy_uri: "",
  website_uri: "",
  support_uri: "",
  label: "",
  lead: "",
  license_type: "per_device",
  genre_name: "",
  is_18plus: 0,
  do_not_sign: 0,
  publish_at: undefined,
  associated_uris: [],
})

function fillForm() {
  if (!identifier) {
    return
  }

  for (const key in form) {
    if (!(key in identifier)) {
      continue
    }

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    form[key as keyof typeof form] = identifier[key as keyof typeof identifier] as never
  }
}

fillForm()
if (!form.associated_uris || !form.associated_uris.length) {
  form.associated_uris = [""]
}

const isSubmitting = ref(false)
async function submit() {
  isSubmitting.value = true

  try {
    await editApp(form)

    const user = useUserStore()
    await user.getData() // user may need to sign new agreements

    navigateTo("/apps/identifiers")
    const { notifySuccess } = useNotification()
    notifySuccess("App identifier was saved")
  } finally {
    isSubmitting.value = false
  }
}

const genres = await listGenres()
const genreOptions = genres.official.map((genre) => ({ label: genre.name, value: genre.name }))
</script>
