<template>
  <Calendar
    v-model="value"
    date-format="yy-mm-dd"
    hour-format="24"
    show-button-bar
    :step-minute="10"
    :manual-input="false"
    :pt="preset"
  />
</template>

<script setup lang="ts">
import Calendar from "primevue/calendar"
import preset from "core/assets/primevue-presets/calendar"
import { isEqual } from "lodash-es"

const props = defineProps<{
  modelValue?: null | number
}>()

const emit = defineEmits<{
  (e: "update:modelValue", value?: null | number): void
}>()

const value = ref<null | Date>()
watchImmediate(
  () => props.modelValue,
  () => {
    if (isEqual(props.modelValue, value.value)) {
      return
    }

    if (typeof props.modelValue === "number") {
      value.value = new Date(props.modelValue * 1000)
      return
    }

    value.value = props.modelValue
  }
)

watchEffect(() => {
  if (value.value instanceof Date) {
    return emit("update:modelValue", +value.value / 1000)
  }

  emit("update:modelValue", value.value)
})
</script>
